import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
import EvanSwitch from "@/components/evan-switch/evan-switch.vue"
import wPicker from "@/components/w-picker/w-picker.vue";
import uniPopup from "@/components/uni-popup/uni-popup.vue"

export default {
	data() {
		return {
			checked1: true,
			time1: '08:00:00',
			cycle1: '每天',
			checked2:true,
			time2: '20:00:00',
			cycle2: '每天',
		}
	},
	components: {
		uniNavBar,
		EvanSwitch,
		wPicker,
		uniPopup
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		onChagne1(e) {
			console.log(e)
		},
		onChagne2(e) {
			console.log(e)
		},
		showPicker(type) {
			this.$refs[type].show();
		},
		showPop(type) {
			if (type == 'cycle1') {
				this.$refs.cycle1.open()
			} else if (type == 'cycle2') {
				this.$refs.cycle2.open()
			}
		},
		onConfirm1(res, type) {
			this.time1 = res.result;
		},
		onConfirm2(res, type) {
			this.time2 = res.result;
		},
		chooseCycle1(msg) {
			this.cycle1 = msg;
		},
		chooseCycle2(msg) {
			this.cycle2 = msg;
		}
	}
}
